export default {
	props:{
		item:Object
	},
	methods:{
		gotoSet(url){
			switch (this.item.type){
				case 'navigateTo':
					this.$emit('tapEvent',url)
					break;
				case 'clear':
					uni.showModal({
						title:'提示',
						content:'是否要清除缓存',
						confirmText:'确认清除',
						success: (res) => {
							if(res.confirm){
								try {
								    uni.clearStorageSync();  
								} catch (e) {
								    console.log(e);
								}
								uni.showToast({
									title:'清除成功'
								})
							}
						}
					})
					
					break;	
				default:
					break;
			}
			
		}
	}
}
